import Controls from '../Controls/Controls';

var originals;
var vol;
var volArea;
var volContainer;
var volBar;
var video;
var isDown = false;



async function getVolumeElement() {
    volArea = document.getElementById('controls--volumeballC');
    vol = document.getElementById('controls--volumeball');
    volContainer = document.getElementById('controls--volumecontainer');
    volBar = document.getElementById('controls--volumebar');
    video = document.getElementById('watchVideo');
    return true;
}


async function getVolumeOriginals(e) {
    return {
        top: parseInt(vol.style.marginTop) || 0,
        mouseY: e.clientY
    }
}

async function VolumeMouseMove(e) {
    e.preventDefault();
    var top;

    if (isDown) {
        if (originals.mouseY < e.clientY) {
            top = originals.top - (originals.mouseY - e.clientY);
        }
        else {
            top = originals.top - (originals.mouseY - e.clientY);
        }
        //console.log(top)
        if (top < 100 && top > 0) {
            let volume = (100 - top) / 100;
            Controls.volume(video, volume);
            vol.style.marginTop = top + 'px';
            volBar.style.height = 100 - top + '%';
        }
        else if ( top >= 100 ) {
            Controls.volume(video, 0);
            vol.style.marginTop = 100 + 'px';
            volBar.style.height = 0 + '%';
        }
        else if ( top <= 0 ) {
            Controls.volume(video, 1);
            vol.style.marginTop = 0 + 'px';
            volBar.style.height = 100 + '%';
        }
    }
}


async function volumeLeave(e) {
    volContainer.removeEventListener('mouseleave', volumeLeave, false);
    volContainer.style.display = 'none';
}


export default {
    VolumeMouseDown: async (e) => {
        isDown = true;
        const gVol = await getVolumeElement();
        
        originals = await getVolumeOriginals(e);

        volContainer.style.display = 'flex';       

        if (originals.top === '') {
            originals.top = 0;
        }

        document.addEventListener("pointermove", VolumeMouseMove, false);
        document.addEventListener("touchmove", VolumeMouseMove, false);

    },
    VolumeMouseUp: async (e) => {
        e.preventDefault();
        if ( isDown ) {
            document.removeEventListener('pointermove', VolumeMouseMove, false);
            document.removeEventListener('touchmove', VolumeMouseMove, false);
            isDown = false;
        }  
    },
    VolumeClick: async (e) => {
        
        if ( e.target.id !== 'controls--volumeball') {
            const rect = document.getElementById('watchCVB').getBoundingClientRect();
            let top = Math.floor(e.clientY - rect.top);
            const gVol = await getVolumeElement();

            if (top < 100 && top > 0) {
                let volume = (100 - top) / 100;
                Controls.volume(video, volume);
                vol.style.marginTop = top + 'px';
                volBar.style.height = 100 - top + '%';
            }
            else if ( top >= 100 ) {
                Controls.volume(video, 0);
                vol.style.marginTop = 100 + 'px';
                volBar.style.height = 0 + '%';
            }
            else if ( top <= 0 ) {
                Controls.volume(video, 1);
                vol.style.marginTop = 0 + 'px';
                volBar.style.height = 100 + '%';
            }
        }
        
    }
}