var timedelay = 1;
//var _delay = setInterval(delayCheck, 500);
var _delay;

function delayCheck() {
    if (timedelay == 3) {
        document.body.style.cursor = 'none';
        const painelControle = document.getElementById('watchControls');
        const progressbar = document.getElementById('progressbar');
        painelControle.style.display = 'none';
        progressbar.style.display = 'none';
        document.getElementById('controls--volumecontainer').style.display = 'none';
        timedelay = 1;
    }
    timedelay = timedelay + 1;
}


export default {
    showControls: (e) => {
        const painelControle = document.getElementById('watchControls');
        const progressbar = document.getElementById('progressbar');
        painelControle.style.display = 'flex'; 
        progressbar.style.display = 'flex';
        document.body.style.cursor = 'default';
        
        clearInterval(_delay);
        timedelay = 1;

        if (e.target.id === "watchScreen") {
            _delay = setInterval(delayCheck, 500);
        } 
    },

    hideControls: () => {
        const painelControle = document.getElementById('watchControls');
        const progressbar = document.getElementById('progressbar');
        painelControle.style.display = 'none';
        progressbar.style.display = 'none';
        document.body.style.cursor = 'none';
        document.getElementById('controls--volumecontainer').style.display = 'none';
    },

    pause: (video) => {
        video.pause();
    },

    play: (video) => {
        video.play();
    },

    skipForward: (video) => {
        video.currentTime += 10;
    },

    skipRewind: (video) => {
        video.currentTime -= 10;
    },

    volume: (video, valor) => {
        video.volume = valor;
    },

    volumeMute: (video) => {
        video.volume = 0;
    },

    volumeUnMute: (video) => {
        const valor = parseInt(document.getElementById('controls--volumebar').style.height) / 100;
        if (isNaN(valor)) {
            video.volume = 1;
        }
        else {
            video.volume = valor;
        }     
    },

    requestFull: () => {
        const elem = document.getElementById('watch');
        if (elem.requestFullscreen) {
            elem.requestFullscreen();
          } else if (elem.webkitRequestFullscreen) {
            elem.webkitRequestFullscreen();
          } else if (elem.msRequestFullscreen) {
            elem.msRequestFullscreen();
          }
    },

    exitFull: () => {
        if (document.exitFullscreen) {
            document.exitFullscreen();
        } else if (document.webkitExitFullscreen) {
            document.webkitExitFullscreen();
        } else if (document.msExitFullscreen) {
            document.msExitFullscreen();
        }
    }
}